class Portfolio extends Component{
	constructor(props) {
			super(props)
			this.state = {
				tokens: ['1','2'],
				loading: true,
				trueCount: ['1','2']
			}
		}
	
	 async componentWillMount() {
				await this.loadWeb3()
				await this.loadBlockchainData()
				await this.client()
				await this.filler()
				//console.log(this.state.tokens[0])
				//console.log(this.state.tokens[1])
				//console.log(this.state.tokens[2])
				//await this.getAssets()
				//window.alert(this.state.id)
			}
			async filler(){
				const a = 0;
				const accounts12 = await window.web3.eth.getAccounts()
				//console.log(this.state.tokens)
			}
			
			//"https://api.etherscan.io/api?module=account&action=tokentx&address="+this.state.account+"&startblock=0&endblock=999999999&sort=asc&apikey=9X1VB3H6AP5DZHKUKUHHFACSTA5HTP2YMY"
			//"https://api.nomics.com/v1/currencies/ticker?key=c34101f14e90e665f78c474309c8f804&convert=USD&per-page=100&page=1"
						
			async client(){
				const response1 = await fetch("https://api.etherscan.io/api?module=account&action=tokentx&address=0xD9a14b6F258B52E4E108d516487E08f7928552d3&startblock=0&endblock=999999999&sort=asc&apikey=9X1VB3H6AP5DZHKUKUHHFACSTA5HTP2YMY")
				const content1 = await response1.json()
				const matrixP = new Array(7).fill(0).map(() => new Array(content1.result.length).fill(0));
				//console.log(matrixP[0].length)
				//console.log(content.result.length)
				//console.log(content.result[0].contractAddress)
				var count = 0;
				for(var i = 0; i < content1.result.length; i++){
						if(i==0){
							matrixP[0][count] = "0xEeeeeEeeeEeEeeEeEeEeeEEEeeeeEeeeeeeeEEeE";
							matrixP[1][count] = "Ethereum";
							matrixP[5][0] = "Ethereum";
							matrixP[2][count] = "ETH";
							matrixP[6][0] = "ETH";
							matrixP[4][count] = "18";
							count = count + 1;
						}
						if(!matrixP[0].includes(content1.result[i].contractAddress)){
							matrixP[0][count] = content1.result[i].contractAddress;
							matrixP[1][count] = content1.result[i].tokenName;
							matrixP[2][count] = content1.result[i].tokenSymbol;
							matrixP[4][count] = content1.result[i].tokenDecimal;
							count = count + 1;
						}
				}
				//console.log(matrixP[2[0]])
				var del = content1.result.length - count;
				matrixP[0].length -= del;
				matrixP[1].length -= del;
				matrixP[2].length -= del;
				//matrixP[3].length -= del;
				//console.log(matrixP[0])
				//console.log(matrixP[1])
				//console.log(matrixP[2])
				this.setState({loading: true})
				var realCount = 1;
				this.setState({account: "0xD9a14b6F258B52E4E108d516487E08f7928552d3"})
				getAddressBalances(window.web3, this.state.account, matrixP[0]).then(balances =>{
					matrixP[3][0] = parseFloat(this.state.ethBalance/Math.pow(10,matrixP[4][0])).toFixed(4);
					for(var j = 1; j < count; j++){
						//matrixP[3][j] = parseFloat((balances[matrixP[0][j]])/Math.pow(10,matrixP[4][j])).toFixed(4);
						var temp = parseFloat((balances[matrixP[0][j]])/Math.pow(10,matrixP[4][j])).toFixed(4);
						if(temp > 0){
							matrixP[3][realCount] = temp;
							matrixP[5][realCount] = matrixP[1][j]; //Name Real
							matrixP[6][realCount] = matrixP[2][j]; //Abbrev Real
							realCount = realCount + 1;
						}
					}
					//matrixP[3].length -= del;
					matrixP[3].length = realCount;
					matrixP[5].length = realCount;
					matrixP[6].length = realCount;
					console.log(matrixP[3])
					console.log(matrixP[3].length)
					this.setState({tokens: matrixP})
					//console.log('1')
					this.setState({loading: false})
					//console.log(this.state.tokens)
				})
				//matrixP[3].length -= del;
				//console.log(matrixP[3][matrixP[0][0].toString()])
				//console.log(matrixP[3][matrixP[0][1]])
				//this.setState({tokens: matrixP})
				//console.log(this.state.tokens)
				//console.log(this.state.tokens[3][1])
			}


			async loadBlockchainData() {
				const web3 = window.web3

				const accounts = await web3.eth.getAccounts()
				//this.setState({ account: accounts[0] })
				this.setState({ account: "0xD9a14b6F258B52E4E108d516487E08f7928552d3" })

				const ethBalance = await web3.eth.getBalance(this.state.account)
				this.setState({ ethBalance })
				

				// Load Token = LockToken
				const networkId =  await web3.eth.net.getId()
			}

			async loadWeb3() {
				if (window.ethereum) {
					window.web3 = new Web3(window.ethereum)
					await window.ethereum.enable()
				}
				else if (window.web3) {
					window.web3 = new Web3(window.web3.currentProvider)
				}
				else {
					window.alert('Non-Ethereum browser detected. You should consider trying MetaMask!')
				}
				
			}

		
			isEth = (randomProp) =>{
				if(randomProp == '0xEeeeeEeeeEeEeeEeEeEeeEEEeeeeEeeeeeeeEEeE'){
					return "Ethereum";
				}
				return "Other Crypto";
			}

			
			toEth(am){
				am = window.web3.utils.fromWei(am, 'Ether')
				am = am.toString()
				am = parseFloat(am).toFixed(5)
				return am;
			}
			
			isPositive(sign,number){
				if(sign == "-"){
					return(<div className="neg">{parseFloat((number.replace(/\"/g, "")).valueOf()*100).toFixed(2)+"%"}</div>);
				}
				else{
					return(<div className="pos">{parseFloat((number.replace(/\"/g, "")).valueOf()*100).toFixed(2)+"%"}</div>)
				}
			}

			greaterThanZero(coin,abrev,balCal){
				console.log(coin + " " + abrev + " " + balCal)
				if(balCal > 0){
					return(
					<><td className="rowSpace">{
						<b>{coin}</b>
					}</td>
					<><td className="rowSpace">{
						<b>{abrev}</b>
					}</td></>
					<td className="rowSpace">{
						<b>{balCal}</b>
					}</td></>);
				}
			}

	
	render(){
		let content
		if(this.state.loading) {
			content = <p id="loader" className="text-center">Loading...</p>
		} else {
			
			content = <form className="forms" onSubmit={(event) => {
					event.preventDefault()
					this.props.checkLocked()
				}}>
				<ReactBootStrap.Table striped borderless hover variant ="dark" className = "tableD">
					<thead>
						<tr>
							<th>{"Coin"}</th>
							<th>{" "}</th>
							<th>{"Balance"}</th>
						</tr>
					</thead>
					<tbody>{
						this.state.tokens[3].map((item,index) => (
						<tr key ={index}>
						
						{this.greaterThanZero(this.state.tokens[5][index],this.state.tokens[6][index],this.state.tokens[3][index])}
											
						</tr>
						))
						
					
					}
					</tbody>
				</ReactBootStrap.Table>
				</form>
		}
		return(
		<div>
		<MenuBar account={this.state.account} />
		<div className ="backgroundColor">
		<div className="fontSize">
		<div className ="homescreenFormat">
		<div className = "Table1">
		
		{content}
		</div>
		</div>
		</div>
		</div>
		</div>
		
				);
	}
}
export default Portfolio;


